import React, { Component } from 'react';

import '../App.css';


class Buttons extends Component {
  constructor() {
    super();
    this.state = { 
         up:"",
         down:"",
         right:"",
         left:""
       }
  }
  
   render() {
  const upStart = () =>{
    this.setState({up: "True"})
  }

  const upStop = () =>{
      this.setState({up: "False"})
    }

  const downStart = () =>{
      this.setState({down: "True"})
    }

  const downStop = () =>{
      this.setState({down: "False"})
    }

  const leftStart = () =>{
      this.setState({left: "True"})
    }

  const leftStop = () =>{
      this.setState({left: "False"})
    }

  const rightStart = () =>{
      this.setState({right: "True"})
    }

  const rightStop = () =>{
     this.setState({right: "False"})
    }
  
  fetch('http://localhost:4000/values',{
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        up : this.state.up,
        down : this.state.down,
        left : this.state.left,
        right : this.state.right
      })
  })
       return (
       <div className="Common">
          <div className="btn PNP">
              <p><button className="Mrgn-btn" type="button">upPNP</button></p>
              <p><button className="Mrgn-btn" type="button">downPNP</button></p>
          </div>

          <div className="App  Arrows">
              <div >
                <button onMouseDown={this.upStart} onMouseUp={this.upStop} className="Mrgn-btn" type="button">up</button>
              </div>
              <div>
                <button onMouseDown={leftStart} onMouseUp={leftStop} className="Mrgn-btn" type="button">left</button>
                <button onMouseDown={rightStart} onMouseUp={rightStop} className="Mrgn-btn" type="button">right</button>
              </div>
              <div>
                 <button onMouseDown={this.downStart} onMouseUp={downStop} className="Mrgn-btn" type="button">down</button>
              </div>
          </div>

          <div className="btn PNP">
             <p> <button className="Mrgn-btn" type="button">upCam</button></p>
             <p><button className="Mrgn-btn" type="button">downCam</button></p>
          </div>

          <div>

          </div>
       </div>
      );
  }
  }

export default Buttons;
